<template>
    <div>
        <h3>Locations Add Page</h3>
        <b-form @submit="onSubmit">
            <b-card
                v-if="errorMessage"
                border-variant="danger"
                text-variant="danger"
                class="mb-3"
            >
                {{ errorMessage }}
            </b-card>
            <b-card
                v-if="successMessage"
                border-variant="success"
                text-variant="success"
                class="mb-3"
            >
                {{ successMessage }}
            </b-card>
            <b-form-group label="Name" label-for="inputName">
                <b-form-input
                    id="inputName"
                    v-model.trim="$v.form.name.$model"
                    :class="{ 'is-invalid': $v.form.name.$error }"
                >
                </b-form-input>
                <b-form-invalid-feedback>
                    This field is required
                </b-form-invalid-feedback>
            </b-form-group>
            <b-form-group label="Address" label-for="inputName">
                <b-form-select :options="addresses"
                    v-model="$v.form.address.$model"
                    :class="{ 'is-invalid': $v.form.address.$error }"
                ></b-form-select>
                <b-form-invalid-feedback>
                    This field is required
                </b-form-invalid-feedback>
            </b-form-group>
            <b-form-group label="Client" label-for="inputName">
                <b-form-select 
                    :options="clients"
                    v-model="$v.form.client.$model"
                    :class="{ 'is-invalid': $v.form.client.$error }"
                ></b-form-select>
                <b-form-invalid-feedback>
                    This field is required
                </b-form-invalid-feedback>
            </b-form-group>
            <b-button type="submit" @click="onSubmit" variant="primary" class="px-4">Add</b-button>
        </b-form>
    </div>
</template>
<script>
import { required } from 'vuelidate/lib/validators';
import Backend from '../../services/backend';

const backend = new Backend();

export default {
    name: 'LoactionsAddEditPage',
    data() {
        return {
            addresses: [],
            clients: [],
            form: {
                name: '',
                client: '',
                address: ''
            },
            successMessage: '',
            errorMessage: ''
        };
    },
    validations: {
        form: {
            name: {
                required
            },
            address: {
                required
            },
            client: {
                required
            }
        }
    },
    created() {
        this.loadValues(); 
    },
    methods: {
        loadValues() {
            backend.getRequest('services/addresses')
                .then(response => {
                    response.data.map(item => {
                        item.value = item.id;
                        delete item.id;
                        item.text = item.name;
                        delete item.name;
                    });
                    this.addresses = response.data;
                })
                .catch(error => {
                    console.log(error);
                });
            backend.getRequest('vbill/clients')
                .then(response => {
                    response.data.map(item => {
                        item.value = item.id;
                        delete item.id;
                        item.text = item.name;
                        delete item.name;
                    });
                    this.clients = response.data;
                })
                .catch(error => {
                    console.log(error);
                });
        },
        clearForm() {
            this.form = {
                name: '',
                client: '',
                address: ''
            };
            setTimeout(() => this.$v.$reset(), 0);
        },
        onSubmit(e) {
            e.preventDefault();
            if (this.$v.$invalid) {
                this.$v.$touch();
                return;
            }
            let data = {
                name: this.name,
                address: this.address,
                client: this.client
            };
            console.log(data);

            backend.postRequest('services/locations/', data)
                .then((response) => {
                    let addressName = response.data.name;
                    this.errorMessage = null;
                    this.successMessage = `${addressName} was added successfully`;
                    this.clearForm();
                })
                .catch((error) => {
                    console.log(error);
                    this.errorMessage = error.response.data.name && error.response.data.name[0] ||
                        'Something went wrong. Please try again later';
                });
        }
    }
};
</script>
